.equ{
    width: 500px;
    flex-wrap: wrap;
    position: absolute;
    top:100px;
    z-index: 2000;
}
.equR{
  right: 15px;
}
.equL{
  left: 15px;
}
.gold{
  width: 200px;
  height: 50px;
  background-color: rgba(22, 21, 26, 0.322);
  border: 1px solid rgb(49, 46, 48);
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top:15px;
  z-index: 2000;
  right: 15px;
  color: rgb(238, 238, 177);
  font-weight: bold;
}
.it {
    background-color: rgba(22, 21, 26, 0.322);
    border: 1px solid rgb(49, 46, 48);
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    position: relative;
  }
.it img {
    height: 70px;
    width: 70px;
    user-select: none;
  }
  .count{
      position: absolute;
      bottom:10px;
      right: 10px;
      font-weight: bold;
      color: rgb(252, 252, 230);
  }