@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.App{
    min-height: 100vh;
    background-color: rgb(2, 1, 0);
    font-family: 'Roboto', sans-serif;
}
.c{
  display: flex;
  justify-content: center;
  align-items: center;
}
.chest{
  max-width: 100%;
  width: 400px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 100;
}
.chest2{
  position: absolute;
  top: 3%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  opacity: 0.1;
  width: 55%;
}
.close{
  z-index: 300;
  background-color: rgba(22, 21, 26, 0.322);
  border: 1px solid rgb(49, 46, 48);
  padding: 10px;
  padding-left: 25px;
  padding-right: 25px;
  font-size: 25px;
  position: absolute;
  bottom: 20%;
}
.zwykla,.srebrna,.zlota,.epicka{
  margin-top: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.zwykla{
 color: rgb(82, 77, 68);
}
.srebrna{
  color: rgb(173, 173, 173);
}
.zlota{
  color: rgb(134, 131, 79);
}
.epicka{
  color: rgb(59, 62, 236);
}
.status{
  position: absolute;
  top: 10%;
  left: 50%;
  font-size: 25px;
  transform: translateX(-50%);
  text-align: center;
  z-index: 1000;
}
.statusA{
  -webkit-animation: status 0.35s ease-in-out;
          animation: status 0.35s ease-in-out;

}
.status_success{
  color: rgb(30, 214, 30);
}
.status_failure{
  color: red;
}
.anm_left{
  -webkit-animation: animL 0.3s;
          animation: animL 0.3s;
}
.anm_right{
  -webkit-animation: animR 0.3s;
          animation: animR 0.3s;
}
@-webkit-keyframes status{
  from{
    transform: translate(-50%,-50px);
    opacity: 0;
  }
  to{
    transform: translate(-50%,0);
    opacity: 1;
  }
}
@keyframes status{
  from{
    transform: translate(-50%,-50px);
    opacity: 0;
  }
  to{
    transform: translate(-50%,0);
    opacity: 1;
  }
}
@-webkit-keyframes animR{
  0%{
    transform: scale(1) translateX(0);
  }
  50%{
    transform: scale(1.1) translateX(30px);
  }
  100%{
    transform: scale(1) translateX(0);
  }
}
@keyframes animR{
  0%{
    transform: scale(1) translateX(0);
  }
  50%{
    transform: scale(1.1) translateX(30px);
  }
  100%{
    transform: scale(1) translateX(0);
  }
}
@-webkit-keyframes animL{
  0%{
    transform: scale(1) translateX(0);
  }
  50%{
    transform: scale(1.1) translateX(-30px);
  }
  100%{
    transform: scale(1) translateX(0);
  }
}
@keyframes animL{
  0%{
    transform: scale(1) translateX(0);
  }
  50%{
    transform: scale(1.1) translateX(-30px);
  }
  100%{
    transform: scale(1) translateX(0);
  }
}

.equ{
    width: 500px;
    flex-wrap: wrap;
    position: absolute;
    top:100px;
    z-index: 2000;
}
.equR{
  right: 15px;
}
.equL{
  left: 15px;
}
.gold{
  width: 200px;
  height: 50px;
  background-color: rgba(22, 21, 26, 0.322);
  border: 1px solid rgb(49, 46, 48);
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top:15px;
  z-index: 2000;
  right: 15px;
  color: rgb(238, 238, 177);
  font-weight: bold;
}
.it {
    background-color: rgba(22, 21, 26, 0.322);
    border: 1px solid rgb(49, 46, 48);
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    position: relative;
  }
.it img {
    height: 70px;
    width: 70px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .count{
      position: absolute;
      bottom:10px;
      right: 10px;
      font-weight: bold;
      color: rgb(252, 252, 230);
  }
.itemBottom{
    width: 50%;
    height: 150px;
    background-color: rgba(22, 21, 26, 0.322);
    border: 1px solid rgb(49, 46, 48);
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.coinValue{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
    align-items: center;
}
.coinValue .v{
    color: rgb(239, 255, 147);
}
.itemImg{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 50px;
}
.itemImg img{
   height: 80px;
}
.bold{
    font-weight: bolder;
    color: rgb(40, 206, 40);
}
*,*::before,*::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
}

