.itemBottom{
    width: 50%;
    height: 150px;
    background-color: rgba(22, 21, 26, 0.322);
    border: 1px solid rgb(49, 46, 48);
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.coinValue{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
    align-items: center;
}
.coinValue .v{
    color: rgb(239, 255, 147);
}
.itemImg{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 50px;
}
.itemImg img{
   height: 80px;
}
.bold{
    font-weight: bolder;
    color: rgb(40, 206, 40);
}